    this.heights = [416, 416, 832, 1664, 3328, 6656];

    this.maxW = 6656;
    this.maxH = 6656;

    let gl;

    try {
      const canvas = document.createElement("canvas");

      gl = canvas.getContext("experimental-webgl");

      if (!gl) {
        gl = canvas.getContext("webgl");
      }
    } catch (error) {}

